.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    flex-direction: column;
}
p {
    font-size: 60px;
}
.buttons {
    display: flex;
    gap: 20px;
}
.btn1 {
    background-color: rgb(57, 134, 57);
  
}
.btn2 {
    background-color: rgb(221, 73, 73);
}
button {
    padding: 20px;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;
    
}
